@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    background-color: #EBECF0;
}

body, html {
    overflow-x: hidden;
    scroll-behavior: smooth;
}

* {
    margin: 0;
    padding: 0;
}

.primary-button a {
    text-decoration: none;
    color: black;
}

.flex-with-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.font-bold {
    font-weight: bold;
}

.n-box1 {
    background: #001220;
    box-shadow: 5px 5px 0px #00070d,
        -5px -5px 0px #001d33;
}

.n-box2 {
    border-radius: 50px;
    background: #EBECF0;
    box-shadow: 5px 5px 0px #cacbce,
        -5px -5px 0px #ffffff;
}

.introduction {
    height: 100vh;
    width: 100%;
    background-position: 100% 100%;
    background-size: cover;
}

.introduction p {
    color: white;
}

.introduction div h1 {
    color: rgba(255, 255, 255, 0.466);
    font-size: 100px;
    font-weight: bold;
}

.introduction div h1:hover {
    color: white;
    cursor: default;
    text-shadow: 0px 0px 15px #fff;
}

.primary-button {
    background-color: white;
    height: 45px;
    border-radius: 25px;
    border: none;
    width: 50%;
}

.primary-button:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.336);
}

.dev-stack {
    color: black;
    transition: all 0.5s;
    padding: 10px;
}

.dev-stack:hover {
    color: white;
    background-color: #001220;
    transform: scale(1.05);
    box-shadow: none;
}

.who-is-michael {
    height: 75vh;
    background-size: 100% 100%;
    color: white;
    margin-bottom: 30px;
}

.who-is-michael:hover {
    transform: scale(1.05);
}

pre {
    font-size: 18px;
    font-family: "Montserrat", sans-serif !important;
}

@media (max-width: 750px) {
    .introduction div h1 {
        font-size: 75px;
    }

    .primary-button {
        width: auto;
        padding: 5px 15px;
    }

    .who-is-michael {
        height: 100vh;
        background-size: 180% 100%;
        background-position: center;
        transition: all 0.5s;
    }

    pre {
        font-size: 13px;
    }
}

@media (max-width: 350px) {
    .introduction div h1 {
        font-size: 55px;
    }

    .primary-button {
        padding: 1px 15px;
    }

    .introduction p {
        font-size: 13px;
    }

}
.menu-icon {
    color: orangered;
    font-size: 70px;
    margin: 20px;
    z-index: 3;
    cursor: pointer;
}

.show-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    right: 0;
    height: 65vh;
    width: 50%;
    border-bottom-left-radius: 50%;
    transition: all 0.5s;
    background-color: #001220;
    opacity: 0.95;
    z-index: 2;
}

.hide-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    right: 0;    
    overflow: hidden;
    height: 0;
    width: 0;
    transition: all 0.5s;
}

ul li {
    color: white;
    list-style: none;
    margin-top: 25px;
}

li a {
    color: white;
    text-decoration: none;
}

.active-link {
    border: 3px solid cyan;
    padding: 5px;
    border-radius: 10px;
}

.active-link a {
    font-weight: bold;
    color: cyan;
}

.contact img {
    height: 50vh;
    width: 100%;
}

input, textarea {
    margin-top: 25px;
    border: none !important;
    background-color: #EBECF0 !important;
    background: #EBECF0;
    box-shadow: 5px 5px 0px #cacbce, -5px -5px 0px #ffffff !important;
    border-radius: 20px !important;
}

.form-control:focus {
    transform: scale(1.02);
}

.form-control {
    box-shadow: none;
}

.contact button {
    background-color: rgba(0, 0, 255, 0.74);
    font-weight: bold;
    color: white;
}

.contact button:hover {
    background-color: blue;
}

.footer-content {
    background-color: #001220;
    text-align: center;
    color: white;
}

.footer-icons svg {
    color: white;
    cursor: pointer;
}

.footer .footer-icon:hover {
    color: cyan;
    transform: scale(1.5);
    cursor: pointer;
}

.letter {
    color: cyan;
    font-weight: bold;
}

@media (max-width: 750px) {
    .show-header {
        height: 100vh;
        width: 100%;
        border-bottom-left-radius: 50%;
        border-top-left-radius: 50%;
        border: 2px solid rgba(255, 255, 255, 0.397);
        transition: width 0.5s;
        z-index: 2;
    }

    .hide-header {
        transition: width 0.5s;
    }

    .menu-icon {
        font-size: 45px;
    }

    .contact {
        margin-top: 0 !important;
        padding: 0 !important;
    }

    .contact-form {
        padding: 15px !important;
        margin: 15px !important;
    }

    .contact button {
        width: 150px;
    }
}
.courosel-parent {
    height: 100vh;
    background-color: #001220;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.gallery {
    height: 200px;
    width: 200px;
    position: relative;
    transform-style: preserve-3d;
    animation: gallery 20s linear infinite;
}

.gallery:hover {
    animation-play-state: paused;
}

.gallery:hover svg {
    opacity: 0.2;
}

.gallery span {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform-style: preserve-3d;
    transform-origin: center;
    transform: rotateY(calc(var(--i) * 45deg)) translateZ(400px);
    transition: all 1s;
}

.gallery span svg {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    -webkit-box-reflect: below 1px linear-gradient(transparent, transparent, #000);
    transition: all 1s;
}

.gallery span svg:hover {
    transition: all 1s;
    transform: scale(1.2);
    cursor: pointer;
    opacity: 1;
}

@keyframes gallery {
    0% {
        transform: perspective(1000px) rotateY(0deg);
    }
    100% {
        transform: perspective(1000px) rotateY(360deg);
    }
}
.projects-intro .primary-button {
    background-color: blue;
    color: white !important;
    font-weight: bold;
}

.project {
    /* height: 500px;
    width: 500px; */
    padding: 20px;
    border: 1px solid rgba(0, 0, 0, 0.719);
    border-radius: 10px;
    margin-top: 25px;
    margin-bottom: 25px;
    cursor: pointer;
    animation: project-animation 10s linear infinite;
}

.project img {
    height: 500px;
    width: 500px;
}

.project .project-content:hover {
    opacity: 1;
    transform: scale(1.02);
}

.project:hover {
    animation-play-state: paused;
}

.project-content {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.80);
    height: 100%;
    color: white;
    font-weight: 500;
    border-radius: 10px;
    opacity: 0;
    transition: all 0.5s;
}

.blog-intro {
    height: 100vh;
    background-size: cover;
    background-position: center;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.blog-intro-content {
    width: 50%;
    color: white;
    border-left: 5px solid cyan;
    padding-left: 30px;
    animation: blog-intro-animation 10s linear infinite;
}

.blog-intro-content h1 {
    font-size: 75px;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: cyan;
    border-bottom: 2px solid white;
}

.blog-intro-content p {
    border-right: 2px solid white;
}

@media (max-width: 750px) {

    .blog-intro-content {
        width: 100%;
        padding: 20px;
        margin: 20px;
    }

    .blog-intro-content h1 {
        font-size: 35px;
        -webkit-text-stroke-width: 1px;
    }
}


@keyframes project-animation {
    0% {
        transform: rotate(-5deg);
    }

    25% {
        transform: rotate(0deg);
    }

    50% {
        transform: rotate(5deg);
    }

    75%{
        transform: rotate(0deg);
    }

    100%{
        transform: rotate(-5deg);
    }    
}

@keyframes blog-intro-animation {
    0% {
        transform: rotate(-5deg);
    }

    25% {
        transform: rotate(0deg);
    }

    50% {
        transform: rotate(5deg);
    }

    75%{
        transform: rotate(0deg);
    }

    100%{
        transform: rotate(-5deg);
    }    
}
